import React, { Component } from 'react';
import { connect } from 'react-redux';
import {actionEditData} from '../../redux/actions/noteAction'
class NoteFormEdit extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ItemChoise: '',
            titleNote: '',
            contentNote: ''
        }
    }
    isChange = (evt) => {
        const name = evt.target.name;
        const value = evt.target.value;
        this.setState({
            [name]: value
        });
    };
    //WARNING! To be deprecated in React v17. Use componentDidMount instead.

    componentDidUpdate(prevProps, prevState) {
        // only update chart if the data has changed
        if (prevProps.ItemChoise !== this.props.ItemChoise) {
            this.setState({
                i: this.props.ItemChoise,
                titleNote: this.props.editItem.titleNote,
                contentNote: this.props.editItem.contentNote
            })
        }
      }
    render() {
        return (
            <div className="col-4">
                <h4>SA NI DUNG NOTE</h4>
                <form onChange={console.log('123')} key={this.props.ItemChoise}>
                    <div className="form-group">
                        <label htmlFor="titleNote">SA NI DUNG NOTE</label>
                        <input defaultValue={this.props.editItem.titleNote} onChange={(evt) => { this.isChange(evt) }} type="text" className="form-control" name="titleNote" id="titleNote" aria-describedby="helpIdTitle" placeholder="Tiu  note" />
                        <small id="helpIdTitle" className="form-text text-muted">in tiu  vo y</small>
                        <textarea defaultValue={this.props.editItem.contentNote} onChange={(evt) => { this.isChange(evt) }} type="text" className="form-control" name="contentNote" id="contentNote" aria-describedby="helpIdTitle" placeholder="Ni dung note" />
                        <small id="helpIdContent" className="form-text text-muted">in ni dung vo y</small>
                    </div>
                    <button type="reset" onClick={() => this.props.actionEditData({titleNote:this.state.titleNote, contentNote:this.state.contentNote}) } className="btn btn-primary btn-block">Lu</button>
                </form>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        editItem: state.note.editItem,
        ItemChoise: state.note.ItemChoise,
        titleForm: state.note.titleForm
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        actionEditData: (editItem) => {
            dispatch(actionEditData(editItem))
        }
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(NoteFormEdit);